// export default ProductDetails
import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { useParams, useNavigate } from 'react-router-dom'
import Slider from "react-slick";
import alertify from "alertifyjs";
import "alertifyjs/build/css/alertify.css";
import { useLoading } from '../../Context/LoadingContext';

function ProductDetails() {

    const { setLoading } = useLoading();

    useEffect(() => {
      setLoading(true);
      setTimeout(() => setLoading(false), 2000); // تحاكي تحميل البيانات
    }, [setLoading]);














    var settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
    };

    const { id } = useParams();
    const navigate = useNavigate(); // لاستخدام التنقل بين الصفحات
    const [productDetails, setProductDetails] = useState({});
    const [isLoading, setIsLoading] = useState(false);

    async function getProductDetails() {
        setIsLoading(true);
        let { data } = await axios.get('https://ecommerce.routemisr.com/api/v1/products/' + id);
        setIsLoading(false);
        setProductDetails(data.data);
    }

    async function addProductToCart(productId) {
        try {
            const { data } = await axios.post('https://ecommerce.routemisr.com/api/v1/cart', {
                productId
            }, {
                headers: {
                    token: localStorage.getItem('token')
                }
            });

            alertify.success(data.message); // إشعار بنجاح الإضافة
            navigate('/cart'); // توجيه المستخدم إلى السلة بعد الإضافة
        } catch (error) {
            alertify.error("حدث خطأ أثناء إضافة المنتج للسلة");
        }
    }

    useEffect(() => {
        getProductDetails();
    }, [id]);

    return (
        <>
            {isLoading ?
                <div className='d-flex align-items-center justify-content-center my-5 py-5'>
                    <i className='fas fa-spin fa-spinner fa-2x'></i>
                </div>
                :
                <div className='row align-items-center py-5 container m-auto'>
                    <div className="col-md-3">
                        <Slider {...settings}>
                            {productDetails.images?.map((img, index) => {
                                return <img key={index} src={img} className='w-100' alt='' />
                            })}
                        </Slider>
                    </div>
                    <div className="col-md-9">
                        <h2 className='mt-2'>{productDetails?.title}</h2>
                        <h5 className='font-sm text-main mt-2'>{productDetails?.category?.name}</h5>
                        <p className='mt-2'>{productDetails?.description}</p>
                        <p className='d-flex justify-content-between mt-2'>
                            <span>{productDetails?.price} EGP</span>
                            <span>
                                <i className='fas fa-star rating-color me-1'></i>
                                <span>{productDetails?.ratingsAverage}</span>
                            </span>
                        </p>
                        <button 
                            onClick={() => addProductToCart(productDetails.id)} 
                            className='btn bg-main text-white w-100 mt-2'>
                            Add To Cart
                        </button>
                    </div>
                </div>
            }
        </>
    )
}

export default ProductDetails;
