import React from 'react'
import Layout from './Component/Layout/Layout'
import { RouterProvider, createHashRouter } from 'react-router-dom'
import Home from './Component/Home/Home'
import Rigester from './Component/Rigester/Rigester'
import Products from './Component/Products/Products'
import Login from './Component/Login/Login'
import AuthContextprovider from './Context/AuthContext'
import ProtectedRoute from './Component/ProtectedRoute/ProtectedRoute'
import AuthProtected from './Component/AuthProtected/AuthProtected'
import ProductDetails from './Component/ProductDetails/ProductDetails'
import Cart from './Component/Cart/Cart'
import Address from './Component/Address/Address'
import Prands from './Component/Prands/Prands'
import Categories from './Component/Categories/Categories'
import Repassword from './Component/Repassword/Repassword'
import Recode from './Component/Recode/Recode'
import { LoadingProvider } from './Context/LoadingContext'
import LoadingScreen from './Component/LoadingScreen/LoadingScreen'



export default function App() {
  const routers = createHashRouter([
    {path:'',element:<Layout/>,children:[
      {path:'login',element: <AuthProtected><Login/></AuthProtected>},
      {path:'register',element: <AuthProtected><Rigester/></AuthProtected>},
      {path:'repassword',element: <AuthProtected><Repassword/></AuthProtected>},
      {path:'recode',element: <AuthProtected><Recode/></AuthProtected>},
      {path:'home',element:<ProtectedRoute><Home/></ProtectedRoute>},
      {path:'products',element:<ProtectedRoute><Products/></ProtectedRoute>},
      {path:'brands',element:<ProtectedRoute><Prands/></ProtectedRoute>},
      {path:'categories',element:<ProtectedRoute><Categories/></ProtectedRoute>},
      {path:'productDetails/:id',element:<ProtectedRoute><ProductDetails/></ProtectedRoute>},
      {path:'cart',element:<ProtectedRoute><Cart/></ProtectedRoute>},
      {path:'address/:cartId',element:<ProtectedRoute><Address/></ProtectedRoute>}
    ]
  }
  ])
  return (
    <>
  <LoadingProvider>
    <AuthContextprovider>
    <LoadingScreen/> 
    <RouterProvider router={routers}></RouterProvider>
    </AuthContextprovider>
  </LoadingProvider>
    </>
  )
}

