import axios from "axios";
import React, { useEffect, useState } from "react";
import Slider from "react-slick";

export default function ProductSlider() {
  var settings = {
    dots: true,
    speed: 500,
    slidesToShow: 7,
    slidesToScroll: 1,
    responsive: [
      {
        breakpoint: 1024, // شاشات التابلت
        settings: {
          slidesToShow: 4,
        },
      },
      {
        breakpoint: 768, // شاشات الموبايل المتوسطة
        settings: {
          slidesToShow: 3,
        },
      },
      {
        breakpoint: 480, // شاشات الموبايل الصغيرة
        settings: {
          slidesToShow: 2,
        },
      },
    ],
  };

  const [category, setCategory] = useState([]);

  async function getCategory() {
    const { data } = await axios.get("https://ecommerce.routemisr.com/api/v1/categories");
    setCategory(data.data);
  }

  useEffect(() => {
    getCategory();
  }, []);

  return (
    <>
      <div className="container m-auto text-center cursor-pointer">
        <Slider {...settings}>
          {category.map((category, index) => (
            <div key={index} className="col-6 col-md-4 col-lg-2 d-flex flex-column align-items-center">
              <div
                className="rounded-circle bg-light d-flex align-items-center justify-content-center"
                style={{ width: 150, height: 150, overflow: "hidden" }}
              >
                <img
                  src={category.image}
                  className="w-100 h-100"
                  style={{ borderRadius: "50%" }}
                  alt={category.name}
                />
              </div>
              <h4 className="mt-2">{category.name}</h4>
            </div>
          ))}
        </Slider>
      </div>
    </>
  );
}
