import axios from 'axios'
import React, { useEffect } from 'react'
import alertify from "alertifyjs";
import "alertifyjs/build/css/alertify.css";
import { Link } from 'react-router-dom'
import { useLoading } from '../../Context/LoadingContext';


export default function Producte({product}) {






  const { setLoading } = useLoading();

  useEffect(() => {
    setLoading(true);
    setTimeout(() => setLoading(false), 2000); // تحاكي تحميل البيانات
  }, [setLoading]);






  const showNotification = (message) => {
    alertify.notify( message,"success", 5, function () {
    });
  };
  
async function addProdactToCart(productId){
  const {data} = await axios.post("https://ecommerce.routemisr.com/api/v1/cart",{
    productId
  },{
    headers:{
      token: localStorage.getItem('token')
    }
  })
  console.log(data)
  showNotification(data.message)
  console.log(localStorage.getItem('token'))
  
}



  return (
    <>

    <div>
            <div className="product overflow-hidden text-center cursor-pointer">
               <Link to={'/productDetails/' + product.id} className='a' >
                <img src={product.imageCover} className='w-100' alt="" />
                <h5 className='font-sm text-main'>{product.category.name}</h5>
                <h4>{product.title.split(" ").slice(0,2).join(" ")}</h4>
                <p className='d-flex justify-content-between'>
                    <span>{product.price}EGP</span>
                    <span>
                        <i className='fas fa-star rating-color me-1'></i>
                        {product.ratingsAverage}
                    </span>
                </p>
                </Link>
               <button onClick={()=> addProdactToCart(product.id)} className='btn bg-main text-white w-100'>+Add To Cart</button>
            </div>
        </div>
    </>
  )
}
