import axios from 'axios'
import React, { useEffect, useState } from 'react'
import Producte from '../Producte/Producte'
import ProductSlider from '../ProductSlider/ProductSlider'
import imageOne from '../../Assit/grocery-banner.png'
import imageTwo from '../../Assit/grocery-banner-2.jpeg'
import imageThree from '../../Assit/2.jpg'
import imageFour from '../../Assit/1.jpg'
import Slider from "react-slick";
import { useLoading } from '../../Context/LoadingContext'


export default function Home() {

  const { setLoading } = useLoading();

  useEffect(() => {
    setLoading(true);
    setTimeout(() => setLoading(false), 2000); // تحاكي تحميل البيانات
  }, [setLoading]);













  var settings = {
    dots: false,
    infinite:true ,
    speed: 500,
    slidesToShow:1,
    slidesToScroll: 2,
    arrows: false

    
  };
    const[products,setProduct]=useState([])
    async function getAllProducte(){
    const {data} = await axios.get('https://ecommerce.routemisr.com/api/v1/products')
    setProduct(data.data)
  }
  useEffect( ()=>{
    getAllProducte()
  },[]

  )
  return (
   <>
        <header>
            <div className="row container g-0 m-auto">
              <div className="col-md-10">
            <Slider {...settings}>
                <div className="">
                    <img src={imageOne} className="w-100" alt="" />
                </div>
                <div className="">
                    <img src={imageTwo} className="w-100" alt="" />
                </div>
                </Slider>
                </div>  


            

                <div className="col-md-2">
                    <img src={imageThree} className="w-100" alt="" />
                    <img src={imageFour} className="w-100" alt="" />
                </div>
            </div>
        </header>

    <div className='mt-3 '><ProductSlider/>
</div>
   <div className="row container m-auto">
    {products.map((product)=>{
        return(
            <div key={product.id} className="col-md-3">
            <Producte product={product}/>
         </div>
        )
    }
       
    )}
   </div>
   </>
  )
}  