import axios, { spread } from 'axios'
import React, { useEffect, useState } from 'react'
import { useLoading } from '../../Context/LoadingContext';

export default function Products() {

  const { setLoading } = useLoading();

  useEffect(() => {
    setLoading(true);
    setTimeout(() => setLoading(false), 2000); // تحاكي تحميل البيانات
  }, [setLoading]);










  const [sProduct,setsProduct]=useState([])
  async function getsProduct() {
    const{data}=await axios.get('https://ecommerce.routemisr.com/api/v1/products')
    setsProduct(data.data)
    console.log(data)
  }
useEffect(()=>{
    getsProduct()
  },[])
  return (<>
<div className="row container m-auto text-center cursor-pointer">
  {sProduct.map((sProduct, index) => (
    <div key={index} className="col-md-4">
      <img style={{ height: 400 }} src={sProduct .imageCover} className="w-100" alt="" />
      <img style={{ height: 50 }} src={sProduct .brand.image} className="w-25" alt="" />
      <h4 className="font-sm text-main">{sProduct .category.name}</h4>
    </div>
  ))}
</div>

        
     </>   
  )
}
