import alertify from 'alertifyjs';
import axios from 'axios';
import { useFormik } from 'formik';
import React from 'react';
import { useNavigate } from 'react-router-dom'; // ✅ استدعاء useNavigate

export default function Recode() {
  const navigate = useNavigate(); // ✅ تعريف المتغير الخاص بالتوجيه

  const { values, handleSubmit, errors, handleChange, touched, handleBlur, isValid } = useFormik({
    initialValues: {
      resetCode: "",
    },
    validationSchema: null,
    onSubmit: async (values) => {
      try {
        let { data } = await axios.post("https://ecommerce.routemisr.com/api/v1/auth/verifyResetCode", values);
        console.log(data);
        
        // ✅ عرض رسالة النجاح
        alertify.notify("Success! Redirecting...", "success", 2, () => {
          navigate('/login'); // ✅ التوجيه للـ Login بعد الإشعار
        });

      } catch (error) {
        console.error("Error:", error);
        alertify.notify("Failed! Try again.", "error");
      }
    },
  });

  return (
    <>
      <div className="shadow rounded-2 my-3 container d-flex justify-content-center align-items-center" style={{ height: 250 }}>
        <form onSubmit={handleSubmit} className="w-100 text-center">
          <h3 className="my-1">Code:</h3>
          <input
            onChange={handleChange}
            value={values.resetCode}
            type="text"
            className="form-control mb-3 w-75 mx-auto"
            id="resetCode"
            name="resetCode"
            onBlur={handleBlur}
          />
          <button className="btn bg-main text-white w-25 d-block mx-auto" type="submit" disabled={!isValid}>
            Submit
          </button>
        </form>
      </div>
    </>
  );
}
