import axios from 'axios';
import React, { useEffect, useState } from 'react';
import CartProduct from '../CartProduct/CartProduct';
import Swal from 'sweetalert2';
import { Link } from 'react-router-dom';
import { useLoading } from '../../Context/LoadingContext';

export default function Cart() {

  const { setLoading } = useLoading();

  useEffect(() => {
    setLoading(true);
    setTimeout(() => setLoading(false), 2000); // تحاكي تحميل البيانات
  }, [setLoading]);












  const[cart,setCart]=useState({})
  const[timeoutId,setTimeoutId]=useState()
  const[cartId,   setcartId]=useState()
  async function getLoggedInCartProduct(){
    const {data}= await axios.get('https://ecommerce.routemisr.com/api/v1/cart',{
      headers:{
        token:localStorage.getItem('token')
      }
    })
    setcartId(data.data._id)
    console.log(data)
    setCart(data)
  }
  function removeItem(productId){
    const swalWithBootstrapButtons = Swal.mixin({
      customClass: {
        confirmButton: "btn btn-success",
        cancelButton: "btn btn-danger"
      },
      buttonsStyling: false
    });
    swalWithBootstrapButtons.fire({
      title: "Are you sure?",
      text: "You won't be able to revert this!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonText: "Yes, delete it!",
      cancelButtonText: "No, cancel!",
      reverseButtons: true
    }).then(async(result) => {
      if (result.isConfirmed) {
        const {data}=await axios.delete('https://ecommerce.routemisr.com/api/v1/cart/'+productId,{
          headers:{
            token:localStorage.getItem('token')
          }
        })
        console.log(data)
        setCart(data)
        swalWithBootstrapButtons.fire({
          title: "Deleted!",
          text: "Your file has been deleted.",
          icon: "success"
        });
      } else if (
        /* Read more about handling dismissals below */
        result.dismiss === Swal.DismissReason.cancel
      ) {
        swalWithBootstrapButtons.fire({
          title: "Cancelled",
          text: "Your imaginary file is safe :)",
          icon: "error"
        });
      }
    });

  }
  function Clearcart(productId){
    const swalWithBootstrapButtons = Swal.mixin({
      customClass: {
        confirmButton: "btn btn-success",
        cancelButton: "btn btn-danger"
      },
      buttonsStyling: false
    });
    swalWithBootstrapButtons.fire({
      title: "Are you sure?",
      text: "You won't be able to revert this!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonText: "Yes, delete it!",
      cancelButtonText: "No, cancel!",
      reverseButtons: true
    }).then(async(result) => {
      if (result.isConfirmed) {
        const {data}=await axios.delete('https://ecommerce.routemisr.com/api/v1/cart/',{
          headers:{
            token:localStorage.getItem('token')
          }
        })
        console.log(data)
        setCart(data)
        swalWithBootstrapButtons.fire({
          title: "Deleted!",
          text: "Your file has been deleted.",
          icon: "success"
        });
      } else if (
        /* Read more about handling dismissals below */
        result.dismiss === Swal.DismissReason.cancel
      ) {
        swalWithBootstrapButtons.fire({
          title: "Cancelled",
          text: "Your imaginary file is safe :)",
          icon: "error"
        });
      }
    });

  }
  useEffect(()=>{
    getLoggedInCartProduct()
  },[])
   function UpdataItems(productId,count){
    clearTimeout(timeoutId)
    setTimeoutId(
      setTimeout(async()=>{
        const {data}=await axios.put('https://ecommerce.routemisr.com/api/v1/cart/'+productId,{
          count
        },{
          headers:{
            token:localStorage.getItem('token')
          }
        })
        console.log(data)
        setCart(data)
      },5000
  
      )
    )
}

  return <>
  {cart.data?.products.length>0?
    
    <div className='my-5'>
      <button onClick={Clearcart} className='btn btn-outline-danger d-block ms-auto'>Clear Cart</button>

      {cart.data?.products.map(( cartproduct , index)=> {
        return <CartProduct UpdataItems={UpdataItems} key={index} removeItem={removeItem} Cartproduct={cartproduct} />
        })}
        <div className='d-flex justify-content-between'>
          <Link to={'/address/'+cartId} className='btn bg-main text-white'>CheckOut</Link>
          <p>Total cart Price: {cart.data?.totalCartPrice} EGP</p>
        </div>

      </div>
:
   <h2 className='alert alert-warning text-center my-5'>No products in your cart</h2>

 }
  </>
}
