import React, { useState, useEffect } from 'react';

export default function CartProduct({ Cartproduct, removeItem, UpdataItems }) {
    console.log("Cartproduct data:", Cartproduct); // راقب البيانات في الكونسول
    
    const [count, setCount] = useState(1);

    useEffect(() => {
        if (Cartproduct?.count) {
            setCount(Cartproduct.count);
        }
    }, [Cartproduct]);

    if (!Cartproduct || !Cartproduct.product) {
        return <p>Loading product...</p>; // رسالة انتظار لو البيانات لسه مش موجودة
    }

    return (
        <div className="cart-product shadow rounded-2 my-3 container">
            <div className="row align-items-center">
                <div className="col-md-2">
                    <img className='w-100' src={Cartproduct?.product?.imageCover || ''} alt="Product" />
                </div>
                <div className="col-md-8">
                    <h2>{Cartproduct?.product?.title || "No title available"}</h2>
                    <h5>{Cartproduct?.product?.category?.name || "No category"}</h5>
                    <p className='d-flex justify-content-between'>
                        <span>{Cartproduct?.price || 0} EGP</span>
                        <span>
                            <i className='fas fa-star rating-color me-1'></i>
                            {Cartproduct?.product?.ratingsAverage || 0}
                        </span>
                    </p>
                    <p>
                        <span className='fw-bolder'>Total Price:</span> {Cartproduct?.price * count || 0} EGP
                    </p>
                </div>
                <div className="col-md-2">
                    <button className='btn text-danger' onClick={() => removeItem(Cartproduct?.product?.id)}>Remove</button>
                    <div className="d-flex align-items-center">
                        <button 
                            disabled={count === 1} 
                            onClick={() => { UpdataItems(Cartproduct?.product?.id, count - 1); setCount(count - 1); }} 
                            className='btn bg-main text-white mx-2'
                        >
                            -
                        </button>
                        <span>{count}</span>
                        <button 
                            onClick={() => { UpdataItems(Cartproduct?.product?.id, count + 1); setCount(count + 1); }} 
                            className='btn bg-main text-white mx-2'
                        >
                            +
                        </button>
                    </div>
                </div>
            </div>
        </div>
    );
}
