import React from "react";
import { useFormik } from "formik";
import axios from "axios";
import { Link } from "react-router-dom";

export default function Repassword() {
  const { values, handleSubmit, errors, handleChange, touched, handleBlur, isValid } = useFormik({
    initialValues: {
      email: "",
    },
    validationSchema: null, // لو عندك مخطط تحقق ضفه هنا، أو حط null لو مش محتاجه
    onSubmit: async (values) => { // ✅ تمرير values صح
      try {
        let { data } = await axios.post("https://ecommerce.routemisr.com/api/v1/auth/forgotPasswords ", values);
        console.log(data);
      } catch (error) {
        console.error("Error:", error);
      }
    },
  });

  return (
    <div className="shadow rounded-2 my-3 container d-flex justify-content-center align-items-center" style={{ height: 250 }}>
            <form onSubmit={handleSubmit} className="w-100 text-center">
<h3>Enter Your Email :</h3>
      <input
        type="email"
        className="form-control mb-3 w-75 mx-auto"
        name="email"
        value={values.email}
        onChange={handleChange}
        onBlur={handleBlur}
        placeholder="Enter your email"
      />
      {errors.email && touched.email && <div>{errors.email}</div>}
      
      <button className="btn bg-main text-white w-25 d-block mx-auto" type="submit" disabled={!isValid}>
      <Link to={'/recode'} className=" bg-main text-white w-100 d-block mx-auto">Send Code</Link>

          </button>

    </form>
    </div>

  );
}

